[data-component="sidebar"] {
  display: flex;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.05); }
  [data-component="sidebar"] .sidebar {
    transition: 1.0s;
    width: 250px;
    background: white;
    padding: 15px; }
    [data-component="sidebar"] .sidebar .menu-items {
      display: flex;
      flex-direction: column; }
      [data-component="sidebar"] .sidebar .menu-items .menu-item {
        line-height: 40px;
        font-weight: 500;
        padding: 0 15px;
        margin-bottom: 5px; }
        [data-component="sidebar"] .sidebar .menu-items .menu-item.active {
          background: #1b7fc9;
          color: white;
          border-radius: 4px; }
        [data-component="sidebar"] .sidebar .menu-items .menu-item .fal {
          font-size: 18px; }
